@mixin mobile {
    @media screen and (min-width:375px) {
        @content;
    }
}

@mixin tablet {
    @media screen and (min-width:768px) {
        @content;
    }
}

@mixin pc {
    @media screen and (min-width:1440px) {
        @content;
    }
}

